  //         property: "og:url",
  //         content: `${conf.donation_fe.conn_type}${conf.donation_fe.ip}${conf.donation_fe.port}/about/${this._id}`
  //       },
  //       {
  //         hid: "og:title",
  //         name: "og:title",
  //         content: `${this.projectName} - ${conf.title_project}`
  //       },
  //       {
